import React, { useEffect, useState } from "react";
import axios from "axios";
import Password from "./Password";

function Creater() {
  const [notes, setNotes] = useState([{}]);
  const [getEdited, setGetEdited] = useState(false);
  const [newTitle, setNewTitle] = useState("");
  const [newContent, setNewContent] = useState("");
  const [deleteNote, setDeleteNote] = useState(false);
  const [deleteButton, setDeleteButton] = useState(-1);
  const [myPassword, setMyPassword] = useState("");
  const proxyServer = "https://keeper-server.onrender.com";
  // const proxyServer = "http://localhost:9000";

  useEffect(() => {
    fetch(proxyServer + "/api/notes")
      .then((response) => response.json())
      .then((notes) => {
        const data = notes.map((note) => ({
          ...note,
          preview:
            note.content.length > 80
              ? note.content.slice(0, 80).trim() + " ..."
              : note.content,
          longString: note.content.length > 80 ? true : false,
        }));
        setNotes(data);
      });
  }, [notes, getEdited, deleteNote, deleteButton, myPassword]);

  useEffect(() => {
    fetch(proxyServer + "/api/users")
      .then((response) => response.json())
      .then((fetchedPasswords) => {
        setMyPassword(fetchedPasswords[0].password);
      });
  }, []);

  const handleFadeIn = (event) => {
    if (event.target.name === "content" && event.target.value === "") {
      setGetEdited(true);
      document.getElementsByClassName("creater-btn")[0].innerHTML = "-";
    }
  };

  const handleFadeOut = () => {
    if (newTitle === "" && newContent === "") {
      setGetEdited(false);
      setNewTitle("");
      setNewContent("");
      document.getElementsByClassName("creater-btn")[0].innerHTML = "+";
    }
  };

  const handleMouseOut = (event) => {
    if (event.target.name === "title" && newTitle === "" && newContent === "") {
      setGetEdited(false);
      setNewTitle("");
      setNewContent("");
      document.getElementsByClassName("creater-btn")[0].innerHTML = "+";
    }
  };

  const handleFiling = (event) => {
    console.log(event.target);
    if (event.target.name === "title") {
      const newValue = event.target.value;
      setNewTitle(newValue);
    }
    if (event.target.name === "content") {
      const newValue = event.target.value;
      setNewContent(newValue);
      if (document.getElementsByClassName("creater-btn")[0].innerHTML === "-") {
        document.getElementsByClassName("creater-btn")[0].innerHTML = "+";
      }
    }
  };

  const handleSubmit = (event) => {
    if (newTitle !== "" && newContent !== "") {
      setNotes((prevNotes) => {
        event.preventDefault();
        const data = {
          title: newTitle,
          content: newContent,
        };
        const url = proxyServer + "/api/notes";
        axios
          .post(url, data)
          .then((response) => console.log(response))
          .catch((event) => console.log(event));

        return [
          ...prevNotes,
          {
            title: newTitle,
            content: newContent,
          },
        ];
      });
    }

    setGetEdited(false);
    document.getElementsByClassName("creater-btn")[0].innerHTML = "+";
    event.preventDefault();
    setNewTitle("");
    setNewContent("");
  };

  const handlePopup = (event) => {
    const id = event.target.name;
    // console.log(id);
    setDeleteNote(true);
    setDeleteButton(id);
  };

  const handleDelete = () => {
    const id = deleteButton;
    console.log(id);
    if (
      document
        .getElementsByClassName("password-input")[0]
        .getAttribute("value") === myPassword
    ) {
      setNotes((prevNotes) => {
        const data = prevNotes.filter((note, index) => index === Number(id))[0];
        const url = proxyServer + "/api/notes/delete";
        axios.post(url, data);

        return prevNotes.filter((note, index) => index !== Number(id));
      });
    }
    setDeleteNote(false);
  };

  const handleExpand = (event) => {
    document.getElementsByClassName("note-content")[event.target.id].innerHTML =
      document.getElementsByClassName("whole-content")[
        event.target.id
      ].innerHTML;
    const btnId = "btn-" + event.target.id;
    document.getElementsByClassName("delete-btn")[btnId].style.opacity = "0";
    const noteId = "note-" + event.target.id;
    document.getElementsByClassName("note-container")[noteId].style.opacity =
      "1";
  };

  const handleHidden = (event) => {
    document.getElementsByClassName("note-content")[event.target.id].innerHTML =
      document.getElementsByClassName("preview-content")[
        event.target.id
      ].innerHTML;
    const btnId = "btn-" + event.target.id;
    document.getElementsByClassName("delete-btn")[btnId].style.opacity = "1";
    const noteId = "note-" + event.target.id;
    document.getElementsByClassName("note-container")[noteId].style.opacity =
      "0.7";
  };

  return (
    <div>
      <div id="fade-in" className="creater-container">
        <form onSubmit={handleSubmit}>
          <input
            onChange={handleFiling}
            onMouseOut={handleMouseOut}
            style={{ display: getEdited ? "inline-block" : "none" }}
            className="creater-input"
            type="text"
            name="title"
            value={newTitle}
            placeholder="  Title"
          ></input>
          <textarea
            onChange={handleFiling}
            onMouseOver={handleFadeIn}
            className="creater-content"
            name="content"
            value={newContent}
            placeholder="  Take notes here ..."
            rows={getEdited ? "5" : "2"}
            style={{
              resize: "none",
              borderTopLeftRadius: getEdited ? "0" : "10px",
              borderTopRightRadius: getEdited ? "0" : "10px",
            }}
          ></textarea>
          <button name="creater-btn" className="creater-btn" type="submit">
            +
          </button>
        </form>
      </div>
      <div className="notes-container" onMouseOver={handleFadeOut}>
        {notes.map((note, index) => (
          <div id={`note-${index}`} className="note-container" key={index}>
            <div style={{ marginBottom: "20px" }}>
              <h2 className="note-title">{note.title}</h2>
              <p
                id={index}
                className="note-content"
                onMouseOver={handleExpand}
                onMouseOut={handleHidden}
              >
                {note.preview}
              </p>
              <p className="preview-content" style={{ display: "none" }}>
                {note.preview}
              </p>

              <p className="whole-content" style={{ display: "none" }}>
                {note.content}
              </p>
            </div>
            <div>
              <button
                id={`btn-${index}`}
                name={index}
                onClick={handlePopup}
                className="btn btn-outline-light btn-sm delete-btn"
              >
                Delete
              </button>
            </div>
          </div>
        ))}
      </div>
      <Password delete={deleteNote} handleDelete={handleDelete} />
    </div>
  );
}

export default Creater;
